.sliderBlock {
	position: relative;
	height: 100%;
	margin-left: 70px;
	margin-top: 89px;
	margin-bottom: 46px;
	overflow: hidden;
}

.sliderBlock_items {
	position: relative;
	display: block;
	width: 100%;
	height: 403px;
}

.sliderBlock_items__itemPhoto {
	position: absolute;
	width: 100%;
	height: 100%;
	opacity: 0;
	z-index: 2;
	transition: opacity 1s;
}

.sliderBlock_items__showing {
	opacity: 1;
	z-index: 3;
}

.sliderBlock_items:after {
	content: "";
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	background: url("https://github.com/BlackStar1991/CardProduct/blob/master/app/img/decorElements/DecorRectangle.png") 50% 50% no-repeat;
	opacity: 0.2;
	z-index: 4;
}

.sliderBlock_controls {
	width: 100%;
}

.sliderBlock_controls__navigatin {
	margin-top: 8px;
	width: 100%;
}

.sliderBlock_controls__wrapper {
	margin: 1% auto;
	width: 110px;
	height: 20px;
}

.sliderBlock_controls__arrow {
	position: relative;
	width: 24px;
	height: 24px;
	cursor: pointer;
	/* border: 1px solid #536dfe; */
	transition: 300ms all;
	/* background-color: #0d2660; */
	border-radius: 50%;
}

.sliderBlock_controls__arrow i {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 50%;
	left: 50%;
	margin-left: -50%;
	margin-top: -50%;
	/* color: #fff; */
	transition: 300ms all;
	font-weight: bold;
	text-align: center;
	font-size: 20px;
	cursor: pointer;
}

.sliderBlock_controls__arrowBackward {
	float: left;
}

.sliderBlock_controls__arrowForward {
	float: right;
}

.sliderBlock_positionControls {
	display: block;
	width: 100%;
	height: 3px;
	margin: 30px auto 0;
}

.sliderBlock_positionControls__paginatorItem {
	float: left;
	width: 18px;
	height: 10px;
	margin-left: 5px;
	background-color: #d7d7d7;
	cursor: pointer;
}

.sliderBlock_positionControls__paginatorItem:first-child {
	margin-left: 31%;
}

.sliderBlock_positionControls__active {
